/* filedrop.module.css */
.fileDropPanel {
  max-width: 350px;
  width: 80vw;
  max-height: 350px;
  height: 80vw;
  margin: auto;
  padding: 16px;
  background-color: #f9f9f9; /* Light background */
  transition: border-color 0.2s ease-in-out;
  overflow: auto;
}

.fileDropPanel:hover,
.fileDropPanel.dragOver {
  border-color: #3f51b5; /* Highlight color when dragging over */
}

.listItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 8px 16px;
}

.listItemText {
  margin-right: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.iconButton {
  margin-left: 8px;
}
